import { Input } from 'antd';
import { useNavigate } from 'react-router-dom';

function Page() {
  const navigate = useNavigate()
  return (
    <>
      <Input.Search
        placeholder="输入bgm条目链接"
        enterButton="GO!"
        size="large"
        onSearch={(url) => {
          url = url.split('/')
          let subject_id = url[url.length - 1]
          let href = './subject.html?subject=' + subject_id
          console.log(href);
          // eslint-disable-next-line no-restricted-globals
          location.href = href
        }}
      />
      <Input.Search
        placeholder="输入bgm用户id"
        enterButton="GO!"
        size="large"
        onSearch={(v) => navigate(v)}
      />
    </>
  );
}

export default Page;